import Prompt from '../../../components/zz-prompt/index.vue'
export default {
	components: {
		Prompt
	},
	data() {

		return {
			activeNames: '1',
			sendCode: '', //发货编号
			historydetailsList: [], //历史发货明细数据
			refuseReason: '', //拒绝原因
			promptVisible: true, //弹出层
			butState: false, //操作按钮
		}
	},
	onLoad: function(option) {
		this.sendCode = option.id
		this.historydetails()
		console.log(this.uuid)
	},
	methods: {
		onChange(event) {
			console.log(event)
			this.activeNames = event.detail
		},
		historydetails() {
			//发货单详情查询
			this.$http.get(this.$store.state.sendgoodsSelect, {
				sendCode: this.sendCode
			}).then(res => {
				this.historydetailsList = res.data.data
				console.log(this.historydetailsList)
				console.log(res)
			})
		},
		cancel() {
			//弹出框取消按钮
			this.promptVisible = true
		},
		clickPromptConfirm(val) {
			//审核驳回

			let data = {
				sendCode: this.historydetailsList.sendCode,
				auditStatus: 'sh03',
				refuseReason: val
			}
			let that = this
			if (val != '') {
				this.$http.post(this.$store.state.saveState, data).then(res => {
					if (res.data.code == 200) {
						uni.showModal({
							title: '提示',
							content: '操作成功',
							showCancel: false,
							success(res) {
								that.promptVisible = true
								// that.dispatchDetailsQuery()
								if (res.confirm) {
									console.log('用户点击确定');
									uni.reLaunch({
										url: 'Home'
									});
								}
							}

						});
						this.butState = true
					}
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '请输入拒绝原因',
					showCancel: false
				});
			}


			console.log(data)
		},
		Refused() {
			//拒绝按钮
			this.promptVisible = false

		},
		accept() {
			let that = this
			//审核通过
			let data = {
				sendCode: this.historydetailsList.sendCode,
				auditStatus: 'sh01'
			}

			uni.showModal({
				title: '操作',
				content: '是否确认该操作？',
				showCancel: true,
				success(res) {
					if (res.confirm) {

						that.$http.post(that.$store.state.saveState, data).then(res => {
							if (res.data.code == 200) {
								that.butState = true
								that.historydetails()
								uni.showModal({
									title: '提示',
									content: '操作成功',
									showCancel: false,
									success(res) {

										if (res.confirm) {
											console.log('用户点击确定');
											uni.reLaunch({
												url: 'Home'
											});
										}
									}

								});
							}
						})
					}
				}

			});



		},

	}
}
